#container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "sidebar main";
  width: 75vw;
  height: 95vh;
}
#main {
  grid-area: main;
}
#new-message {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 20px 0 20px 10px;
  margin: 0;
  border-top: 1px solid #3f3f3f;
  background-color: white;
}
#new-message .new-message-input {
  height: 30px;
  font-size: 15px;
  margin-top: 10px;
  background-color: whitesmoke;
}
#sidebar {
  grid-area: sidebar;
  padding: 5px 0 0 5px;
  border-right: 3px solid #3f3f3f;
  height: 100%;
}
#sidebar .sidebar-user {
  padding-top: 10px;
}
#messages-list .messages-list-ul {
  position: fixed;
  height: 80%;
  width: 80%;
  overflow-y: scroll;
}

.message {
  padding: 10px 0 10px 10px;
  overflow-wrap: anywhere;
}

.message .message-author {
  font-size: 20px;
}

.message .message-time {
  font-size: 10px;
}

